import {
	mapState,
	mapMutations
} from 'vuex';
const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();
let time1 = '';
import qiniuUploader from '../../utils/qiniuUploader.js'; //七牛sdk
import request from '../../utils/request.js';
const app = getApp();

export default {
	data() {
		return {
			id: '',
			imgArr: [],
			shoeboxImgArr: [],
			shoeboxImgResArr: [],
			qiniuToken: '',
			is_value: '',
			voice: '',
			is_recode: false,
			recode_load: false,
			is_recode_end: false,
			time: 0,
			result_time: '--:--',
			isplay: false,
			remarks: '',
			checked: [],
			totalPrice: 0,
			scheme: []
		}
	},
	onLoad() {
		request('/api/auth/getQiniuToken', 'POST', {}).then(res => {
			this.qiniuToken = res.data;
		});
		request('/api/auth/getOrder', 'POST', {
			bar_id: uni.getStorageSync('shapecode') || this.shapecode
		}).then(res => {
			this.shoeboxImgArr = res.data[0].end_image === null ? [] : JSON.parse(res.data[0].end_image);
			this.shoeboxImgResArr = res.data[0].end_image === null ? [] : JSON.parse(res.data[0].end_image);
			this.sound = res.data[0].sound;
			this.upShapeCode(res.data[0].bar_id);
			this.upJSName(res.data[0].username);
			console.log(res);
		})
	},
	computed: {
		...mapState(['jsName', 'shapecode', 'jsId'])
	},
	methods: {
		...mapMutations(['upShapeCode', 'upJSName']),
		isPlayImg(isplay) {
			const ext = isplay ? "gif" : "png";
			let path = "../../static/images/play_bg.";
			const fullPath = path + ext;
			return fullPath;
		},
		// 添加鞋盒正面照图片
		add_shoeboxImg() {
			const that = this;
			let imgArr = this.shoeboxImgArr;
			console.log(imgArr);
			uni.chooseImage({
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					that.shoeboxImgArr = imgArr.concat(res.tempFilePaths);
				}
			})
		},
		// 添加修复或不可修复的图片
		add_img() {
			let that = this;
			let imgArr = that.imgArr;
			uni.chooseImage({
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					that.imgArr = imgArr.concat(res.tempFilePaths);
				}
			})
		},
		// 删除图片
		deteleImg(e) {
			let index = e.currentTarget.dataset.index;
			let arr = this.imgArr;
			arr.splice(index, 1)
			this.imgArr = arr
		},
		// 添加备注
		add_remarks(e) {
			let value = e.detail.value;
			this.remarks = value;
		},
		// 判断是否开始录音
		isRecode(is_recode) {
			if (is_recode) {
				this.stopRecode();
			} else {
				this.startRecode();
			}
		},
		// 开始录音
		startRecode() {
			const options = {
				duration: 10000, //指定录音的时长，单位 ms
				sampleRate: 16000, //采样率
				numberOfChannels: 1, //录音通道数
				encodeBitRate: 96000, //编码码率
				format: 'mp3', //音频格式，有效值 aac/mp3
				frameSize: 50, //指定帧大小，单位 KB
			}
			//开始录音
			recorderManager.start(options);
			recorderManager.onStart(() => {
				console.log('recorder start')
			});
			//错误回调
			recorderManager.onError((res) => {
				console.log(res);
			})
			this.recode_load = true;
			this.is_recode = true;
		},
		// 结束录音
		stopRecode() {
			let that = this;
			//停止录音
			recorderManager.stop();
			recorderManager.onStop((res) => {
				console.log(res)
				this.time = Math.round((res.duration / 1000));
				this.is_recode = false;
				this.voice = res.tempFilePath;
				this.is_recode_end = true;
				this.recode_load = false;
				console.log('停止录音', res.tempFilePath)
			})
		},
		// 播放录音
		playRecode() {
			let that = this;
			let time = this.time;
			innerAudioContext.src = that.voice;
			innerAudioContext.play();
			let num = 0;
			this.isplay = true
			time1 = setInterval(() => {
				num++
				console.log(time)
				if (num > time) {
					clearInterval(time1);
					innerAudioContext.stop();
					that.isplay = false
				} else {
					let result_time = parseInt(num / 60) + ":" + parseInt((parseFloat(num / 60) -
						parseInt(num / 60)) * 60);
					this.result_time = result_time;
				}
			}, 1000)
		},
		// 停止播放
		stop_play() {
			clearInterval(time1);
			innerAudioContext.stop();
			this.isplay = false
		},
		// 删除音频
		remove_record() {
			console.log(11);
			this.voice = ''
			this.is_recode = false
			this.recode_load = false
			this.is_recode_end = false
			this.time = 0
			this.result_time = '--:--'
			this.isplay = false
		},
		// 上传图片到七牛
		upImgs(imgArr, qiuniuToken) {
			let that = this;
			let resultImgArr = [];

			const arr = [];

			imgArr.forEach(item => {
				if (!/static\.tosneaker\.com/ig.test(item)) {
					arr.push(item);
				}
			});

			if (arr.length === 0) {
				return Promise.resolve(imgArr);
			}

			return new Promise((resolve, reject) => {
				arr.forEach(item => {
					var imgName = item.substr(30, 50);
					qiniuUploader.upload(item, res => {
						resultImgArr.push(res.imageURL);
						that.resultImgArr = resultImgArr;
						if (resultImgArr.length == arr.length) {
							resolve(resultImgArr)
						}
					}, error => {
						reject(error)
					}, {
						region: 'NCN',
						uploadURL: 'https://upload-z1.qiniup.com',
						domain: app.globalData.qiniuImgUrl,
						key: 'uploads/wash/' + imgName,
						uptoken: qiuniuToken
					})
				})
			})
		},
		// 上传录音
		upRecord(file, qiuniuToken) {
			let that = this;
			return new Promise((resolve, reject) => {
				var recordName = file.substr(30, 50);
				qiniuUploader.upload(file, res => {
					resolve(res)
				}, error => {
					reject(error)
				}, {
					region: 'NCN',
					uploadURL: 'https://upload-z1.qiniup.com',
					domain: app.globalData.qiniuImgUrl,
					key: 'uploads/record/' + recordName,
					uptoken: qiuniuToken
				})
			})
		},
		// 重新选择
		is_return() {
			uni.redirectTo({
				url: '../check/check',
			})
		},
		// 提交按钮
		is_submit() {
			uni.showLoading({
				mask: true,
				title: "加载中..."
			});
			if (this.shoeboxImgArr.length == 0) {
				uni.hideLoading()
				uni.showToast({
					title: '至少添加一张修复完成图片',
					icon: "none"
				});
				return
			}
			if (this.shapecode === '') {
				uni.hideLoading()
				uni.showToast({
					title: '条形码为必填项',
					icon: "none"
				});
				return
			}
			let that = this;
			let sound = '';
			if (that.voice !== "") {
				that.upRecord(that.voice, that.qiniuToken).then(res => {
					sound = res.imageURL;
					console.log(sound);
					that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(end_image => {
						end_image = [...new Set(end_image.concat(...this.shoeboxImgResArr))];
						console.log(end_image);
						request("/api/auth/setDone", "POST", {
							wash_id: that.jsId,
							bar_id: this.shapecode,
							end_image,
							remarks: that.remarks,
							sound
						}).then(res => {
							uni.hideLoading();
							uni.showToast({
								title: res.data.message,
								icon: "none",
								duration: 1000,
								complete() {
									if (res.data.code == 200) {
										setTimeout(() => {
											uni.navigateTo({
												url: "../sweep/sweep"
											});
										}, 1000);
									}
								}
							})
						})
					}).catch(error => {
						uni.hideLoading();
						uni.showToast({
							title: '图片上传失败',
							icon: "none"
						})
					});
				}).catch((res) => {
					uni.hideLoading()
					uni.showToast({
						title: '录音上传失败',
						icon: "none"
					})
				})
			} else {
				that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(end_image => {
					end_image = [...new Set(end_image.concat(...this.shoeboxImgResArr))];
					console.log(end_image);
					request("/api/auth/setDone", "POST", {
						wash_id: that.jsId,
						bar_id: that.shapecode,
						end_image,
						remarks: that.remarks,
						sound
					}).then(res => {
						uni.hideLoading();
						uni.showToast({
							title: res.data.message,
							icon: "none",
							duration: 1000,
							complete() {
								setTimeout(() => {
									uni.navigateTo({
										url: "../sweep/sweep"
									});
								}, 1000);
							}
						})
					})
				}).catch(error => {
					uni.hideLoading();
					uni.showToast({
						title: '图片上传失败',
						icon: "none"
					})
				});
			}
		}
	}
}
